@media only screen and (max-width: 767px){

h2.mt-4.aos-init.aos-animate {
    line-height: 45px !important;
    font-size: 1.8rem !important;
    padding-top: 12px;
}

.input-group {
    display: none;
}

button.button {
    display: none !important;
}

section#header {
    min-height: 35vh !important;
    padding-bottom: 75px !important;
}

.search-icon i.fas.fa-search {
    position: absolute;
    right: 5%;
    top: 20%;
    color: var(--white);
    background: var(--primary);
    font-size: 16px;
    border-radius: 50%;
    padding: 7px;
    display: inline-block !important;
}

}

#header p {
    /* color: #9a9a9a; */
    color: #e4e3e3;
    font-size: 14px;
    line-height: 26px;
}

.search-icon i.fas.fa-search {
display: none;
}
