#category {
    background: #f9fcff;
    padding-bottom: 55px;
}

#category a:hover{
    color: #6610f2;
}

#category i {
    font-size: 65px;
}

.f-category {
    -webkit-transition: all .3s linear;
    -moz-transition: all .3s linear;
    -ms-transition: all .3s linear;
    -o-transition: all .3s linear;
    transition: all .3s linear;
    border: 1px solid #f1f1f1;
    margin-top: -1px;
    background: #fff;
    padding: 20px;
    text-align: center;
    /* overflow: hidden; */
    display: block;
    margin-right: -1px;
}

.f-category .icon {
    width: 64px;
    height: 64px;
    display: inline-block;
    border-radius: 50%;
    margin-top: 5px;
    margin-bottom: 15px;
    transform: scale(1);
    -moz-transform: scale(1);
    -webkit-transform: scale(1);
    -webkit-transition: all .3s linear;
    -moz-transition: all .3s linear;
    -ms-transition: all .3s linear;
    -o-transition: all .3s linear;
    transition: all .3s linear;
}

.bg-color-1 {
    background: #26ae61;
}

.card {
    border: none;
}

.f-category h3 {
    font-size: 18px;
    line-height: 32px;
    -webkit-transition: all .3s linear;
    -moz-transition: all .3s linear;
    -ms-transition: all .3s linear;
    -o-transition: all .3s linear;
    transition: all .3s linear;
    color: #333;
}

.f-category a {
    display: block;
}

.f-category:hover {
    background: #f9fcff;
}

