@media (max-width: 767.99px){
    .rsm-scroll{
        width: 90%;
        box-sizing: initial;
        padding: 15px;
        margin: 0 -15px;
        flex-wrap: inherit;
        overflow-x: scroll;
        flex-flow: row !important;
        display: flex;
    }
}

@media (max-width: 767.99px){
    .spl-box {
        margin-left: 15px;
    }
}