#DetailPage .section {
    padding: 60px 0;
}

#DetailPage .job-detail .sideber {
    padding: 15px;
    box-shadow: 0 0 14px rgb(191 191 191 / 24%);
}

#DetailPage .job-detail .content-area {
    padding: 15px 20px;
    box-shadow: 0 0 14px rgb(191 191 191 / 24%);
}

#DetailPage .job-detail .content-area h4 {
    font-size: 30px;
    font-weight: 500;
    margin-bottom: 30px;
}

#DetailPage .job-detail .content-area p {
    margin-bottom: 15px;
}

#DetailPage .job-detail p {
    font-size: 14px;
    color: #9a9a9a;
    line-height: 26px;
    font-family: rubik, sans-serif;
    margin-bottom: 0;
}

#DetailPage .job-detail .content-area h5 {
    font-size: 20px;
    font-weight: 500;
    margin: 15px 0;
}

#DetailPage .job-detail .content-area ul li {
    margin-bottom: 10px;
}

#DetailPage .job-detail .sideber .widghet {
    margin-bottom: 30px;
}

#DetailPage .job-detail .sideber .widghet h3 {
    font-size: 26px;
    margin-bottom: 20px;
    color: #333;
    font-weight: 500;
}

#DetailPage .job-detail .sideber .subscribe-form {
    margin-top: 15px;
}

#DetailPage .job-detail .subscribe-form {
    width: 100%;
    display: table;
    height: 100%;
}

#DetailPage .job-detail .footer-social li {
    display: inline-block;
    margin-right: 12px;
}

#DetailPage .job-detail .sideber .share-job .meta-tag {
    margin-top: 20px;
}

#DetailPage .job-detail .sideber .share-job .meta-tag .meta-part {
    display: inline-block;
    margin-right: 5px;
}

#DetailPage .page-header {
    padding-top: 5rem;
}

