#DetailPage h3.product-title {
    color: white;
}

#DetailPage p.brand {
    color: rgb(255, 255, 255)
}

#DetailPage .tags {
    color: white;
}

#DetailPage .breadcrumb-wrapper {
    margin-bottom: 1.8rem;
}

#DetailPage h5.Company-name {
    font-size: 22px;
}

#DetailPage .sideber .subscribe-form .form-group .btn-common {
    position: absolute;
    top: 0;
    right: 0;
    padding: 7px 15px;
    height: 39px;
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    background: 0 0;
    background: #26ae61;
}

#DetailPage .location-container {
    padding-left: 20px;
    font-style: italic;
}

#DetailPage .share-job {
    position: relative;
}

#DetailPage .share-job .form-control{
    height: 40px !important;
}

#DetailPage .sideber {
    margin-top: 32px;
}

#DetailPage .page-header .breadcrumb-wrapper .img-wrapper {
    position: relative;
    width: 10rem;
    text-align: center;
    display: inline-block;
    max-height: 115px;
    height: min-content;
    line-height: 70px;
    background-color: rgb(255 255 255 / 50%);
    box-shadow: 0 0 7px rgb(203 203 203 / 23%);
}

#DetailPage .img-fluid {
    max-width: 7rem !important;
}

#DetailPage .footer-social li .facebook {
    background-color: #3b5998;
}

#DetailPage .footer-social li .twitter {
    background-color: #55acee;
}

#DetailPage .footer-social li .linkedin {
    background-color: #007bb5;
}

#DetailPage .footer-social li a {
    background-color: #26ae61;
    border-radius: 50%;
    color: #fff;
    height: 32px;
    line-height: 35px;
    text-align: center;
    width: 32px;
    display: inline-block;
    -webkit-transition: all .4s ease-in-out;
    -moz-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}